import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServersService } from '../../servers.service';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { NzMessageService } from 'ng-zorro-antd';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from '@angular/forms';

@Component({
  selector: 'app-role',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.less']
})
export class RoleComponent implements OnInit {
  public api_list = '/api/RoleManage/GetRoleList'; // api
  public api_add = '/api/RoleManage/CreateRole'; // api
  public api_update = '/api/RoleManage/EditRole'; // api
  public api_delete = '/api/RoleManage/DeleteRole?id='; // api

  //
  public displayData: any = []; //
  public isLoading = false; //
  public Page = 1;          //
  public Rows = 10;         //
  public total = 1;         //
  public Sord = null;       //
  public OrderBy = null;    //
  public loading = true;    //
  public Sidx = '0';
  public colData = null;

  //modal 
  public modelTitle = ''; //
  public validateForm: FormGroup; //
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' }); //post
  public isVisible = false; //
  public roleId = ''; //id

  //
  public appList:any = [];
  public searchApp:any = null;
  public breadcrumb = '';


  //
  constructor(public server: ServersService, private http: HttpClient, private fb: FormBuilder, private msg: NzMessageService) {
    this.validateForm = this.fb.group({
      roleName: [null, [Validators.required]],
      description: [null],
    });
  }

  ngOnInit() {
    //
    this.server.getAppList().subscribe((data) => {
      this.appList = data;
      this.searchApp = data[0];
      //
      this.getDataList();
    })
  }
  //
  getDataList(){
    let options = {
      api: this.api_list,
      params: {
        Sidx: '0',
        Page: this.Page,
        Rows: this.Rows,
        AppId: this.searchApp.id
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
    })
  }

  //
  appChange(event){
    this.getDataList();
  }
  //
  searchData(keyword?, reset: boolean = false): void {
    this.isLoading = true;
    if (reset) {
      this.Page = 1;
    }
    this.loading = true;
    this.getList(keyword, this.Page, this.Rows, this.Sidx).subscribe((data: any) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
      this.isLoading = false;
    });
  }
  getList(keyword, pageIndex: number = 1, pageSize: number = 10, Sidx): Observable<{}> {
    let params: any = {}
    params.Name = keyword ? keyword : '';
    params.AppId = this.searchApp.id;
    if (this.Page) {
      params.Page = this.Page;
    }
    if (this.Rows) {
      params.Rows = this.Rows;
    }
    if (this.Sidx) {
      params.Sidx = this.Sidx;
    }
    if (this.Sord) {
      params.Sord = this.Sord;
    }
    let options = {
      api: this.api_list,
      params: params
    }
    return this.server.getRxjsData(options);
  }
  //
  showModal(): void {
    this.isVisible = true;
    this.roleId = '';
    //
    this.resetForm();
  }
  //
  handleOk(): void {
    this.isVisible = false;
  }
  //
  handleCancel(): void {
    this.isVisible = false;
  }
  resetForm(): void {
    this.validateForm.reset();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsPristine();
      this.validateForm.controls[key].updateValueAndValidity();
    }
  }
  //
  editRow(item) {
    this.roleId = item.roleId;
    //
    let initformData = new Observable<any>((observer) => {
      this.validateForm.setValue({ roleName: item.roleName, description: item.description });
      observer.next();
    })
    initformData.subscribe(() => {
      this.isVisible = true;
      this.modelTitle = '';
    })
  }
  //
  submitForm(value): void {
    let paramsObj: any = value;
    if (this.roleId) {
      paramsObj.roleId = this.roleId;
    }
    paramsObj.appId = this.searchApp.id;
    let options = {
      api: this.roleId ? this.api_update : this.api_add,
      params: value,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisible = false;
      this.searchData();
    });

  }

  //
  deleteRow(item): void {
    const options = {
      api: this.api_delete + item.roleId,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.searchData();
    });
  }

}
