import { Component, OnInit, Input, Output, EventEmitter, Pipe, PipeTransform } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServersService } from '../../servers.service';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { NzMessageService} from 'ng-zorro-antd';
import { OAuthService, JwksValidationHandler } from 'angular-oauth2-oidc';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators
} from '@angular/forms';

@Component({
  selector: 'app-application',
  templateUrl: './application.component.html',
  styleUrls: ['./application.component.less']
})
export class ApplicationComponent implements OnInit {
  @Output('sendData') sendData = new EventEmitter<any>();
  public api_list = '/api/AppManage/GetAppList'; // api
  public api_add = '/api/AppManage/CreateApp'; // api
  public api_update = '/api/AppManage/EditApp'; // api
  public api_delete = '/api/AppManage/DeleteApp?id='; // api

  //
  public displayData: any = []; //
  public isLoading = false; //
  public Page = 1;          //
  public Rows = 10;         //
  public total = 1;         //
  public Sord = null;       //
  public OrderBy = null;    //
  public loading = true;    //
  public Sidx = '0';
  public colData = null;

  //modal 
  public modelTitle = ''; //
  public validateForm: FormGroup; //
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' }); //post
  public isVisible = false; //
  public appId = ''; //id
  public breadcrumb = '';

  //
  constructor(public server: ServersService, private http: HttpClient, private fb: FormBuilder, private msg: NzMessageService, private oauthService: OAuthService) {
    this.validateForm = this.fb.group({
      appName: [null, [Validators.required]],
      description: [null],
    });
  }

  ngOnInit() {
    //
    this.getDataList();
  }
  //
  getDataList(){
    let options = {
      api: this.api_list,
      params: {
        Sidx: '0',
        Page: this.Page,
        Rows: this.Rows,
      }
    }
    this.server.getRxjsData(options).subscribe((data) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
    })
  }
  //
  searchData(keyword?,reset: boolean = false): void {
    this.isLoading = true;
    if (reset) {
      this.Page = 1;
    }
    this.loading = true;
    this.getList(keyword,this.Page, this.Rows, this.Sidx).subscribe((data: any) => {
      this.loading = false;
      this.total = data.total;
      this.colData = data.pageResult.data;
      this.displayData = [...this.colData];
      this.isLoading = false;
    });
  }
  getList(keyword,pageIndex: number = 1, pageSize: number = 10, Sidx): Observable<{}> {
    let params: any = {}
    params.Name = keyword? keyword:'';
    if (this.Page) {
      params.Page = this.Page;
    }
    if (this.Rows) {
      params.Rows = this.Rows;
    }
    if (this.Sidx) {
      params.Sidx = this.Sidx;
    }
    if (this.Sord) {
      params.Sord = this.Sord;
    }
    let options = {
      api: this.api_list,
      params: params
    }
    return this.server.getRxjsData(options);
  }
  //
  showModal(): void {
    this.isVisible = true;
    this.appId = ''; 
    //
    this.resetForm();
  }
  //
  handleOk(): void {
    this.isVisible = false;
  }
  //
  handleCancel(): void {
    this.isVisible = false;
  }
  resetForm(): void {
    this.validateForm.reset();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[key].markAsPristine();
      this.validateForm.controls[key].updateValueAndValidity();
    }
  }
  //
  editRow(item) {
    this.appId = item.appId;
    //
    let initformData = new Observable<any>((observer) => {
      this.validateForm.setValue({ appName: item.appName, description: item.description });
      observer.next();
    })
    initformData.subscribe(() => {
      this.isVisible = true;
      this.modelTitle = '';
    })
  }
  //
  submitForm(value): void {
    let paramsObj: any = value;
    if (this.appId) {
      paramsObj.appId = this.appId;
    }
    let options = {
      api: this.appId ? this.api_update : this.api_add,
      params: value
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.isVisible = false;
      this.searchData();
    });

  }
 
  //
  deleteRow(item):void{
    const options = {
      api: this.api_delete + item.appId,
    }
    let postData = this.server.postRxjsData(options);
    postData.subscribe(() => {
      this.searchData();
    });
  }

}
